import BScroll from 'better-scroll'
import Bubble from '@/components/common/plugin/bubble'
import { getRect } from '@/components/common/plugin/dom'
const COMPONENT_NAME = 'BScroll'
export default {
  name: COMPONENT_NAME,
  components:{
    BScroll,
    Bubble
  },
  props:{
    value: Number,
    named:{
      type:String,
      default:null
    },
    startY:{
      type:Number,
      default:0
    },
    issnap:{
      type:Boolean,
      default:false
    },
    momentum:{
      type:Boolean,
      default:true
    },
    bounce:{
      type:Boolean,
      default:true
    },
    probeType: {
      type: Number,
      default: 1
    },
    click: {
      type: Boolean,
      default: true
    },
    scrollX: {
      type: Boolean,
      default: false
    },
    listenScroll: {
      type: Boolean,
      default: false
    },
    pullDownRefresh: {
      type: null,
      default: false
    },
    pullUpLoad: {
      type: null,
      default: false
    },
    pulldown: {
      type: Boolean,
      default: false
    },
    beforeScroll: {
      type: Boolean,
      default: false
    },
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
 data() {
    return {
      index:-1,
      currentIndex:this.index,
      snap:false,
      beforePullDown: true,
      isRebounding: false,
      isPullingDown: false,
      isPullUpLoad: false,
      pullUpDirty: true,
      pullDownStyle: '',
      bubbleY: 0
    }
  },
  created() {
    this.pullDownInitTop = -50
  },
  mounted() {
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  computed:{
    pullUpTxt() {
      const moreTxt =this.pullUpLoad.txt.more 
      const noMoreTxt =this.pullUpLoad.txt.noMore 
      return this.pullUpDirty ? moreTxt : noMoreTxt
    },
    refreshTxt() {
   const moreTxt1 = this.pullDownRefresh.txt.more 
    const noMoreTxt1 = this.pullDownRefresh.txt.noMore 
      return this.pullDowmDirty ? moreTxt1 : noMoreTxt1
    }
  },
  methods: {
      // better-scroll
    _initScroll() {
      if (!this.$refs[this.named]) {
        return
      }
      let domWidth=window.innerWidth
      if(this.issnap){
          this.snap={
              threshold: 0.1,stepX:domWidth,loop:false,
              easing: {
               style: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
               fn: function(t) {
                console.log(t)
                 return t * (2 - t)
                       }
                   }
             }
        }
      let options={
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        scrollY: !this.scrollX,
        snap:this.snap,
        startY:this.startY,
        pullDownRefresh: this.pullDownRefresh,//
        pullUpLoad: this.pullUpLoad,//
        scrollbar:false,
        momentum:this.momentum,
        bounce: this.bounce,// true 
      }
      /*swiper+ xdom*/
      if(this.scrollX){
        this.index = this.value
        let xcontent=this.$refs.wrapper.firstChild
        let childs=xcontent.lastElementChild.children
        let width = 0;
        for (let i = childs.length - 1; i >= 0; i--) {
            childs[i].style.width=domWidth+'px'
             width+= domWidth
        }
        xcontent.style.width=width+'px'
       
      }else{
        /*y*/
        if (this.$refs.listWrapper && (this.pullDownRefresh || this.pullUpLoad)) {
          let width =  `${getRect(this.$refs[this.named]).height + 1}px`
           this.$refs.listWrapper.style.minHeight =width
        }
      }
        this.scroll = new BScroll(this.$refs[this.named],options)
      /* end */
        if(this.scrollX){
        this.scroll.on('scrollEnd', this._onScrollEnd)
        }
        if (this.pullDownRefresh) {
          this._initPullDownRefresh()
        }

        if (this.pullUpLoad) {
          this._initPullUpLoad()
        }
    },
    /**/
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable()
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable()
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    },
    /* emitv-model*/
    _onScrollEnd() {
      // this.index = this.currentIndex
       this.index = this.scroll.getCurrentPage().pageX
    },
    /*  */
    /*emit + animate*/
    _initPullDownRefresh() {
      this.scroll.on('pullingDown', () => {
        this.beforePullDown = false
        this.isPullingDown = true
        this.$emit('pullingDown')
      })

      this.scroll.on('scroll', (pos) => {
        if (!this.pullDownRefresh) {
          return
        }
        if (this.beforePullDown) {
          this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop)
          this.pullDownStyle = `top:${Math.min(pos.y + this.pullDownInitTop, 10)}px`
        } else {
          this.bubbleY = 0
        }

        if (this.isRebounding) {
          this.pullDownStyle = `top:${10 - (this.pullDownRefresh.stop - pos.y)}px`
        }
      })
    },
    /* emitvue*/
    _initPullUpLoad() {
      this.scroll.on('pullingUp', () => {
         this.isPullUpLoad = true
        this.$emit('pullingUp')
      })
    },
    forceUpdate(dirty) {
      if (this.pullDownRefresh && this.isPullingDown) {
         this.isPullingDown = false
         this.pullDowmDirty = dirty
         this._reboundPullDown().then(() => {
          this._afterPullDown()
        })
      } else if (this.pullUpLoad && this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.scroll.finishPullUp()
        this.pullUpDirty = dirty
        this.refresh()
      } else {
        this.refresh()
      }
    },
    _reboundPullDown() {
      const {stopTime = 600} = this.pullDownRefresh
      return new Promise((resolve) => {
        setTimeout(() => {
          this.isRebounding = true
          this.scroll.finishPullDown()
          resolve()
        }, stopTime)
      })
    },
    _afterPullDown() {
      setTimeout(() => {
        this.pullDownStyle = `top:${this.pullDownInitTop}px`
        this.beforePullDown = true
        this.isRebounding = false
        this.refresh()
      }, this.scroll.options.bounceTime)
    }

  },
  watch: {
    currentIndex(val,oldVal){
          this.scroll.goToPage(this.index,0,0)
          this.$emit('input', val)
          this.$emit('on-slide-change',val,oldVal)
    }, 
    /*v-model props*/
    index(val){
      this.currentIndex = val
    },
    value(val){
       this.index = val
    }
  }
}



