import Vue from 'vue'
import Scroll from '@/components/common/plugin/BScroll'
import {mapActions,mapState} from 'vuex'
import CNewslist from '@/components/Newlist'
    export default{
        props:{
            swlist:Array,
        },
        data(){
          return {
              listCon:Array,
              valindex:Number,
              type:String,
              pullDownRefresh: true,//
              pullDownRefreshThreshold: 90,
              pullDownRefreshStop: 40,
              pullUpLoad: true,//
              pullUpLoadThreshold: 0,
              pullUpLoadMoreTxt: '',
              pullUpLoadNoMoreTxt: '',
              scrollToTime: 700,
              scrollToEasing: 'bounce',
              scrollToEasingOptions: ['bounce', 'swipe', 'swipeBounce'],
              items: [],
              itemIndex: 0
          }
        },
    components: {
        Scroll,
        CNewslist,
    },
    mounted(){
     this.DataScreenHeight()
    },
    computed:{
      sindex:{
        get(){
          this.valindex = this.$store.state.type
          return this.valindex
        },
         set(val){
            this.$store.commit('LOG_TYPE', val)
        },    
      },
    ...mapState([
        'list',
        'navList',
        'newsLength',
        'ifReturnState',
        'positionY',
      ]),
      scrollbarObj: function () {
        return this.scrollbar ? {fade: this.scrollbarFade} : false
      },
      pullDownRefreshObj: function () {
        return this.pullDownRefresh ? {
          threshold: parseInt(this.pullDownRefreshThreshold),
          stop: parseInt(this.pullDownRefreshStop),
          txt:{more:this.pullUpLoadMoreTxt+this.newsLength,noMore:this.pullUpLoadNoMoreTxt}
        } : false
      },
      pullUpLoadObj: function () {
        return this.pullUpLoad ? {
          threshold: parseInt(this.pullUpLoadThreshold),
          txt:{more: this.pullUpLoadMoreTxt+this.newsLength, noMore:this.pullUpLoadNoMoreTxt}
        } : false
      },   
  },
  watch:{
        valindex(){
            this.listCon=this.list[this.swlist[this.valindex].type]
          },

  },
  methods:{
    ...mapActions([
        'getNews',
        'getMoreNews',
        'LOG_TYPE',
        'refreshNews',
        'SAVE_POSITION',
      ]),
    DataScreenHeight(){
       let fot = document.querySelector('#app .weui-tabbar').clientHeight
       let hed = document.querySelector('#app .header').clientHeight
       this.$refs.wrapper.style.height=(window.innerHeight-fot-hed)+"px"
    },
    /*router*/
    onSliderChange(val){
       let type=this.type = this.navList[val].type
      this.$router.push(`/HOME/${type}`)
      if(this.list[type].length<1){
            this.getNews({
                      type:type
                  })
      }
    },
    onPullingDown(){
      let type =  this.navList[this.valindex].type
      this.refreshNews({
          type:type
      })
      if(this.ifReturnState){
        setTimeout(()=>{
         this.$refs.scroll[this.valindex].forceUpdate(true)
        },900)
      }else{
         this.$refs.scroll[this.valindex].forceUpdate()
      }

      },
  onPullingUp() {
        let type =  this.navList[this.valindex].type
        this.getMoreNews({
            type:type
        })
        if(this.ifReturnState){
          setTimeout(()=>{
           this.$refs.scroll[this.valindex].forceUpdate(true)
          },900)
        }else{
           this.$refs.scroll[this.valindex].forceUpdate()
        }

      },
   },
   
   beforeDestroy(){
    this.$store.commit('SAVE_POSITION',this.$refs.scroll[this.valindex].scroll.absStartY)
     // console.log(this.$store.state.position)
   }
}



